import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { ref, onValue, query, orderByChild, limitToLast } from 'firebase/database';
import { database } from '../firebase';

const Home = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    // Lấy danh sách bài viết từ Realtime Database
    const postsRef = query(ref(database, 'posts'), orderByChild('createdAt'));

    const unsubscribe = onValue(postsRef, (snapshot) => {
      const postsData = snapshot.val() || {};

      // Chuyển đổi đối tượng thành mảng và sắp xếp theo thời gian tạo (mới nhất trước)
      const postsArray = Object.keys(postsData).map(id => ({
        id,
        ...postsData[id]
      })).sort((a, b) => b.createdAt - a.createdAt);

      setPosts(postsArray);

      // Lấy danh sách danh mục
      const uniqueCategories = [...new Set(postsArray.map(post => post.category))];
      setCategories(uniqueCategories);

      setLoading(false);
    });

    // Hủy đăng ký lắng nghe khi component bị hủy
    return () => unsubscribe();
  }, []);

  // Lọc bài viết theo danh mục
  const filteredPosts = selectedCategory === 'all'
    ? posts
    : posts.filter(post => post.category === selectedCategory);

  return (
    <div className="home">
      <h1 className="text-center mb-3">Blog Tin Tức</h1>

      {/* Bộ lọc danh mục */}
      <div className="category-filter mb-3">
        <div className="container">
          <button
            className={`${selectedCategory === 'all' ? 'btn-primary' : 'btn-secondary'}`}
            onClick={() => setSelectedCategory('all')}
          >
            Tất cả
          </button>

          {categories.map(category => (
            <button
              key={category}
              className={`${selectedCategory === category ? 'btn-primary' : 'btn-secondary'}`}
              onClick={() => setSelectedCategory(category)}
              style={{ marginLeft: '10px' }}
            >
              {category}
            </button>
          ))}
        </div>
      </div>

      {loading ? (
        <div className="loading">
          <div className="spinner"></div>
        </div>
      ) : filteredPosts.length > 0 ? (
        <div className="post-list">
          {filteredPosts.map(post => (
            <div key={post.id} className="post-card">
              {post.imageUrl && (
                <img src={post.imageUrl} alt={post.title} className="post-image" />
              )}
              <div className="post-content">
                <Link to={`/post/${post.id}`} className="post-title">
                  <h2>{post.title}</h2>
                </Link>
                <div className="post-meta">
                  <span>{post.category}</span>
                  <span>Tác giả: {post.authorName}</span>
                </div>
                <div className="post-meta">
                  <span>Lượt xem: {post.viewCount || 0}</span>
                  <span>
                    {post.averageRating ? (
                      <span>⭐ {post.averageRating.toFixed(1)}/5 ({post.ratingCount})</span>
                    ) : (
                      <span>Chưa có đánh giá</span>
                    )}
                  </span>
                </div>
                <div className="post-excerpt">
                  {post.content.substring(0, 150)}...
                </div>
                <Link to={`/post/${post.id}`} className="btn btn-primary">
                  Đọc tiếp
                </Link>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <p className="text-center">Không có bài viết nào trong danh mục này.</p>
      )}
    </div>
  );
};

export default Home;
